@import url(https://fonts.googleapis.com/css?family=Raleway:100;600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.day-list {
    border-top: 1px solid black;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.day-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    width: calc(100% / 5);
}
.day-container:last-child .day-name,
.day-container:last-child .day-number {
    border-right: none;
}
.day-container > div:not(.active-column) {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 50px;
}
.day-container:first-child .day-details.bordered {
    border-left: none;
    width: 100%;
}
.sf-cutoff,
.lf-cutoff,
.bf-cutoff {
    background-color: #464548;
    color: #fff;
    width: calc(100% - 1px);
}
.bl-cutoff {
    background-color: #E60541;
    color: #fff;
}
.release-day {
    background: repeating-linear-gradient(
        45deg,
        #fff,
        #fff 10px,
        #f2f2f2 10px,
        #f2f2f2 20px
    );
}
.day-details p {
    font-size: 14px;
    margin: 0;
}
.day-details .day-description {
    font-size: 12px;
}
.day-name,
.day-number {
    border-top: 1px solid black;
    border-right: 1px solid black;
}

/* active state */
.active-column {
    background-color: rgba(255, 0, 255, .25);
    background: repeating-linear-gradient(
        45deg,
        rgba(230, 5, 65, 0.25),
        rgba(230, 5, 65, .25) 10px,
        rgba(233, 36, 89, .35) 10px,
        rgba(233, 36, 89, .35) 20px
    );
    height: 100%;
    max-height: 345px;
    position: absolute;
    top: 100px;
    left: 0;
    bottom: 0;
    width: calc(100% / 10);
}
.active-column[data-col-position='1'] {
    left: 0;
}
.active-column[data-col-position='2'] {
    left: calc(100% / 10);
}
.active-column[data-col-position='3'] {
    left: calc(100% / 10 * 2);
}
.active-column[data-col-position='4'] {
    left: calc(100% / 10 * 3);
}
.active-column[data-col-position='5'] {
    left: calc(100% / 10 * 4);
}
.active-column[data-col-position='6'] {
    left: calc(100% / 10 * 5);
}
.active-column[data-col-position='7'] {
    left: calc(100% / 10 * 6);
}
.active-column[data-col-position='8'] {
    left: calc(100% / 10 * 7);
}
.active-column[data-col-position='9'] {
    left: calc(100% / 10 * 8);
}
.active-column[data-col-position='10'] {
    left: calc(100% / 10 * 9);
}
.week-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.week-container {
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    width: calc(100%/ 4);
}
.week-container:last-child {
    border-right: 1px solid black;
}

/* display 2 week only */
.week-container {
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    width: calc(100%/ 2);
}
.branch-container {
    display: flex;
    flex-direction: row;
}
.branch-develop,
.branch-release {
    display: flex;
    flex-direction: column;
}
.branch-develop {
    width: calc(100% / 10 * 5);
}
.branch-develop:first-child {
    width: calc(100% / 10 * 3);
}
.branch-develop:last-child {
    width: calc(100% / 10 * 2);
}
.branch-release {
    border-right: 1px solid white;
    border-left: 1px solid white;
    width: calc(100% / 10 * 5);
}
.action-container {
    display: flex;
    flex-direction: row;
}
.action-wrapper-develop,
.action-wrapper-release {
    display: flex;
    flex-direction: column;
}
.action-wrapper-develop {
    background-color: #60B99A;
    width: calc(100% / 10 * 5);
    justify-content: center;
}
.action-wrapper-develop:first-child {
    width: calc(100% / 10 * 3);
}
.action-wrapper-develop:last-child {
    width: calc(100% / 10 * 2);
}
.action-wrapper-release {
    width: calc(100% / 10 * 5);
}
.nested-action {
    display: flex;
    flex-wrap: wrap;
}
.nested-action .action:first-child {
    flex-basis: 100%;
}
.action {
    display: flex;
    justify-content: center;
    align-items: center;
}
.action.regression {
    background-color: #F1EFA5;
}
.action.defects {
    color: #fff;
    font-weight: 700;
    width: calc(100% / 5 * 3);
    background: repeating-linear-gradient(
        45deg,
        #F85D31,
        #F85D31 10px,
        #F9714B 10px,
        #F9714B 20px
    );
}
.action.blockers {
    color: #fff;
    width: calc(100% / 5 * 2);
    background: repeating-linear-gradient(
        45deg,
        #E60541,
        #E60541 10px,
        #EC4471 10px,
        #EC4471 20px
    );
}
.action.defects p,
.action.blockers p {
    font-weight: 700;
}
.cert-container {
    display: flex;
    flex-direction: column;
}
.cert-branch {
    background-color: #3b3a3e;
    color: #e6e6e6;
}
.cert-branch p {
    margin: 6px 0;
    font-size: 12px;
}
body {
  font-family: 'Open Sans', sans-serif;
}
.App {
  text-align: center;
  width: calc(100vw - 80px);
  margin: 0 auto;
  font-size: 16px;
  position: relative;
}

.App-header {
  background-color: #313034;
  font-family: 'Raleway', sans-serif;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: white;
}
.drink-wrapper {
    display: flex;
    margin: 40px 0;
    padding: 0 40px;
}
.drink-container {
    display: flex;
    flex-direction: row;
}
.drink-image {
    background-color: #ddd;
}
.drink-image > img {
    display: block;
    height: auto;
    padding: 4px;
    width: 250px;
}
.drink-info {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    padding: 0 0 10px 30px;
    justify-content: center;
    text-align: left;
}
.drink-title {
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-size: 40px;
    margin: 0;
}
.drink-glass {
    color: #999;
    font-style: italic;
    margin: 0 0 0 40px;
}
.drink-ingredients {
    list-style: none;
    padding: 0;
}
.drink-instructions {
    margin: 0;
}
